.home_bgText__2mscO {
  width: 100%;
  max-width: 1000px;
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/opacity/see-through */
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  color: white;
  font-family: "Roboto Mono", monospace;
  font-weight: bold;
  font-size: 2rem;
  border: 3px solid #f1f1f1;
  border-radius: 5px;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  z-index: 2;
  padding: 1rem;
  text-align: center;
  transition: 200ms all ease-in-out;
  -webkit-animation: home_mymove__23fSi 2s;
          animation: home_mymove__23fSi 2s;
}

@media only screen and (max-width: 900px) {
  .home_bgText__2mscO {
    width: 70%;
    font-size: 1.5rem;
  }
}

@media only screen and (max-width: 650px) {
  .home_bgText__2mscO {
    width: 70%;
    font-size: 1.3rem;
  }
}

@media only screen and (max-width: 380px) {
  .home_bgText__2mscO {
    width: 60%;
    font-size: 1.2rem;
    letter-spacing: 2px;
  }
}

@-webkit-keyframes home_mymove__23fSi {
  from {
    transform: translateY(100%);
    transform: translate(-50%, 50%);
  }
  to {
    transform: translateY(0);
    transform: translate(-50%, -50%);
  }
}

@keyframes home_mymove__23fSi {
  from {
    transform: translateY(100%);
